body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  -webkit-animation: App-fade-in 2s ease-out;
          animation: App-fade-in 2s ease-out;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.App-mobile {
  background-image: url(/static/media/bg_gradient_mobile.97124f17.jpg);
}

.App-desktop {
  background-image: url(/static/media/bg_gradient.9038107b.jpg);
}

@-webkit-keyframes App-fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes App-fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.tickets-link {
  display: inline-block;
  margin: 1em 0;
  padding: 0.5em 1em;
  background-color: #598fbb;
  color: white;
  text-decoration: none;
  border-radius: 3px;
  transition: background-color 0.3s ease;
}

.tickets-link:hover {
  background-color: #bcd1d4;
}

